@use 'base/variables' as v;
@use 'base/mixins' as m;

.actions {
    margin-top: v.$separacion;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
    }

    a {
        color: v.$blanco;
        display: block;
        text-align: center;
        text-decoration: underline;
    }
}