html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  font-family: "Roboto Mono", monospace;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

body::-webkit-scrollbar {
  display: none;
  /* for Chrome, Safari, and Opera */
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.container-submit {
  display: flex;
  justify-content: flex-end;
  margin: 0;
}

h1, h2, h3 {
  font-family: "Roboto Mono", monospace;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.title {
  padding: 2rem 0;
  background-color: #FFBA08;
  box-shadow: rgba(255, 255, 255, 0.4) 0px 2px 4px, rgba(255, 255, 255, 0.3) 0px 7px 13px -3px, rgba(255, 255, 255, 0.2) 0px -3px 0px inset;
}
.title .web-title {
  font-family: "Lobster", cursive;
  font-size: 10rem;
  color: #1a1b15;
}
.title .copy {
  font-size: 3rem;
  color: #1a1b15;
}

.buttons {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 5rem;
}
.buttons .guest {
  margin-top: 5rem;
  grid-column: 1/3;
}

.text-home {
  margin-top: 10rem;
  text-align: center;
}
.text-home .remember {
  text-decoration: underline;
}

.container-title {
  padding: 2rem;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  border-radius: 2rem;
  background-color: #370617;
  margin-bottom: 3rem;
}

.page-name {
  text-align: center;
  margin: 2.5rem 0;
  font-weight: 700;
}

.page-details {
  text-align: center;
}

@media (min-width: 768px) {
  .container-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.container-app .image {
  height: 25rem;
  background-image: url(../img/bg-barbershop.jpg);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .container-app .image {
    height: auto;
  }
}
.container-app .app {
  width: 100%;
  margin: 0 auto;
  padding: 2rem 2rem;
}
@media (min-width: 768px) {
  .container-app .app {
    overflow: scroll;
  }
}

.bar {
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.bar .boton {
  display: flex;
  justify-content: center;
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  align-items: center;
  width: 100%;
  border-radius: 1rem;
  margin: 0;
}
@media (min-width: 768px) {
  .bar .boton {
    width: auto;
  }
}
.bar .boton:hover {
  background-color: black;
  cursor: pointer;
}

.services-bar {
  margin-bottom: 1.25rem;
}
@media (min-width: 768px) {
  .services-bar {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .services-bar .boton {
    margin: 0;
  }
}

.section {
  padding: 5rem 0;
  display: none;
}
.section .text-align {
  text-align: center;
}

.show-section {
  display: block;
}

.hide-section {
  display: none;
}

.list-services {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .list-services {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.list-services .service {
  background-color: #FFFFFF;
  padding: 3rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
}
.list-services .service.selected {
  background-color: #436b84;
}
.list-services .service.selected p {
  color: #FFFFFF;
}
.list-services .service:hover {
  transform: scale(1.05);
  cursor: pointer;
}
.list-services .service p {
  color: #1a1b15;
  margin: 0 0 1.25rem 0;
}
.list-services .service .service-price {
  color: #436b84;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}

.list-products {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .list-products {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.list-products .product {
  background-color: #FFFFFF;
  padding: 3rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
}
.list-products .product.selected {
  background-color: #436b84;
}
.list-products .product.selected p {
  color: #FFFFFF;
}
.list-products .product:hover {
  transform: scale(1.05);
  cursor: pointer;
}
.list-products .product p {
  color: #1a1b15;
  margin: 0 0 1.25rem 0;
}
.list-products .product .product-price {
  color: #436b84;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}

.content-sum p {
  margin-bottom: 1rem;
}
.content-sum span {
  color: #436b84;
  font-weight: 700;
  font-size: 2.4rem;
}

.service-container {
  border: 1px solid #e1e1e1;
  padding: 1rem;
  margin-bottom: 2rem;
}

.product-container {
  border: 1px solid #e1e1e1;
  padding: 1rem;
  margin-bottom: 2rem;
}

.reservations, .services {
  list-style: none;
  padding: 0;
}
.reservations li, .services li {
  padding: 1rem;
  border-top: 1px solid #FFFFFF;
}
.reservations li p, .services li p {
  font-weight: 700;
  color: #436b84;
}
.reservations li span, .services li span,
.reservations li .service,
.services li .service {
  font-weight: 400;
  color: #FFFFFF;
}

.form {
  width: 100%;
  margin-top: 5rem;
}

.field {
  display: flex;
  margin-bottom: 2rem;
  flex-direction: column;
  padding: 1rem;
  border-radius: 1.5rem;
  box-shadow: rgba(255, 255, 255, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.field label {
  margin-bottom: 1rem;
}
.field input {
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.field input:disabled {
  background-color: #cfd1c4;
  color: #1a1b15;
  cursor: not-allowed;
}

.boton {
  display: flex;
  justify-content: center;
  background-color: #f48c06;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  align-items: center;
  width: 100%;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #c26f05;
  cursor: pointer;
}

.button-delete {
  display: flex;
  justify-content: center;
  background-color: #D00000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  align-items: center;
  width: 100%;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .button-delete {
    width: auto;
  }
}
.button-delete:hover {
  background-color: #9d0000;
  cursor: pointer;
}

.actions {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .actions {
    display: flex;
    justify-content: space-between;
  }
}
.actions a {
  color: #FFFFFF;
  display: block;
  text-align: center;
  text-decoration: underline;
}

.alert {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 1rem;
}
.alert.error {
  background-color: #D00000;
}
.alert.success {
  background-color: #329f00;
}
.alert.pending {
  background-color: #F7DC6F;
  -webkit-text-stroke: 0.5px #1a1b15;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #f48c06;
  color: #FFFFFF;
  padding: 2rem 1rem;
  border: none;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  font-weight: 700;
  transition: background-color 300ms ease-in-out;
  font-size: 2rem;
  border: 1px solid #FFFFFF;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #a96104;
}
.tabs button.current-tab {
  background-color: #FFFFFF;
  color: #f48c06;
}

.pagination {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .pagination {
    display: flex;
    justify-content: space-between;
  }
}

.hide {
  visibility: hidden;
}